







import { Component, Vue } from 'vue-property-decorator';
import Success from './Success.vue';
import Failure from './Failure.vue';

@Component({
  name: 'Home',
  components: {
    Success,
    Failure,
  },
})
export default class Home extends Vue {
  data = null;

  isWin = 0;

  async mounted() {
    const { phone } = this.$route.query;
    if (!phone) {
      this.$router.replace({ name: 'login' });
      return false;
    }
    try {
      const data = await this.$xhr.api.get('/users/lottery', {
        params: {
          phone,
        },
      });
      if (!data.switch) {
        this.$toast('还没有开奖');
        setTimeout(() => {
          this.$router.replace({ name: 'login' });
        }, 2000);
        return;
      }
      if (data.isWin) {
        this.data = data;
        this.isWin = 1;
      } else {
        this.isWin = 2;
      }
    } catch (error) {
      this.$toast(error && error.msg);
    }
  }
}
